.container {
    margin-right: auto;
    margin-left: auto;
    padding-right: 20px;
    padding-left: 20px;

    @media screen and (min-width: 480px) {
        width: 480px;
    }

    @media screen and (min-width: 768px) {
        width: 768px;
        padding-right: 35px;
        padding-left: 35px;
    }

    @media screen and (min-width: 1440px) {
        width: 1440px;
        padding-right: 80px;
        padding-left: 80px;
    }
}

.section {
    padding-top: 60px;
    padding-bottom: 60px;

}